/* ======================================================
*  Mixins - debug
*  =====================================================*/

// add a red outline to element
@mixin d($color: red, $width: 3px, $style: solid) {
    outline: $width $style $color;
}

// outline and add text over element
@mixin trace($xs-content: 'trace', $sm-content: null, $md-content: null, $lg-content: null) {
    position: relative;

    &:before {
        background-color: #fff;
        border: 3px solid red;
        color: red;
        content: $xs-content;
        font-size: 12px;
        padding: 0 5px;
        position: absolute;
        right: 5px;
        top: 5px;
        z-index: 99999;

        @if $sm-content != null {
            @media (min-width: $screen-sm-min) {
                content: $sm-content;
            }
        }

        @if $md-content != null {
            @media (min-width: $screen-md-min) {
                content: $md-content;
            }
        }

        @if $lg-content != null {
            @media (min-width: $screen-lg-min) {
                content: $lg-content;
            }
        }
    }
}

// highlight broken html
// @todo    improve this
// @see     https://github.com/yahoo/debugCSS/blob/master/debugCSS.css
@mixin errors() {
    a a,
    img:not([src]),
    img[src=""],
    img[src="#"],
    ol > *:not(li),
    ul > *:not(li),
    dl > *:not(dt):not(dd),
    *[class*=\\],
    *[class*=\.],
    *[class*=\#],
    *[id*=\\],
    *[id*=\.],
    *[id*=\#] {
        @include trace('Error');
    }
}

