/* ======================================================
*  Forms
*  =====================================================*/

textarea {
    max-width: 100%;
    min-height: 120px;
}

label {
    font-weight: normal;

    &[data-toggle="modal"] {
        cursor: help;
    }
}

.form-control {
    @include focus-ring($gray-light);
    box-shadow: none;
}

/* Form style
---------------------------------------------- */
.form-horizontal {
    label {
        padding-top: 10px;
    }
}

/* Form specific
---------------------------------------------- */
.form-contact {
    margin-top: 40px;
}

.card-type {
    display: inline;
    width: 54px;
    padding: 6px 0;
    opacity: 0.3;
    transition: opacity 0.5s;
}

.card-type.selected {
    opacity: 1;
}

/* Errors
---------------------------------------------- */
.form-error {
    @extend .alert;
    @extend .alert-danger;
    text-align: left;
}

/**
* Show error on form submit
*/
.ng-submitted {
    .form-control.ng-invalid-required {
        @extend .form-error;
        margin-bottom: inherit;
    }
}

/**
* Show error if input is raped and still invalid
*/
.ng-touched.form-control {
    &.ng-invalid-required,
    &.ng-invalid, {
        @extend .form-error;
        margin-bottom: inherit;
    }
}

.input-invalid {
    @extend .form-error;
    margin-bottom: inherit;
}

