/* Layout */

.tpl-order-services {
    .main-header .navbar-right {
        display: none;
    }

    .navbar .order-need-help {
        display: block;
    }

    .footer .second-row {
        display: none;
    }
}

/* Body */

.tpl-order-services {
    .section-services {
        .container {
            position: relative;
        }

        @media screen and (max-width: $screen-xs-max) {
            padding-top: 15px;
            padding-bottom: 15px;
        }
    }

    h1 {
        margin: 0 0 63px;
        color: $text-color;
        font-size: 36px;
        font-weight: 300;
        text-align: center;
    }

    .order-services-form {
        @media (max-width: $screen-xs-max) {
            button[type="submit"] {
                display: block;
                width: 100%;
            }
        }
        @media (min-width: $screen-sm-min) {}
        @media (min-width: $screen-md-min) {}
        @media (min-width: $screen-lg-min) {}
    }

    input[type="radio"] {
        display: none;
    }

    .question {
        display: none;
        table-layout: fixed;
        width: 780px;
        margin-bottom: 20px;
        border-radius: 5px;
        border: 1px solid #b7b7b7;
        background: #fff;

        &:last-child {
            margin-bottom: 0;
        }

        &.first {
            display: table;
        }

        > p,
        > label {
            display: table-cell;
            vertical-align: middle;
            padding: 32px 0;

            @media screen and (max-width: $screen-xs-max) {
                display: block;
            }
        }

        .image {
            width: 200px;

            @media screen and (max-width: $screen-xs-max) {
                display: none;
            }
        }

        .image img {
            margin: auto;
        }

        .content {
            padding-right: 21px;
            color: $text-color;
            text-align: left;

            @media screen and (max-width: $screen-xs-max) {
                padding: 20px;
            }
        }

        .answer-boolean {
            width: 73px;
            margin: 0;
            border-left: 1px solid #b7b7b7;
            color: $text-color;
            font-weight: 500;
            cursor: pointer;

            @media screen and (max-width: $screen-xs-max) {
                width: 100%;
                border-left: 0;
                border-top: 1px solid #b7b7b7;
            }
        }

        .answer-boolean:hover,
        input[name^="answers"]:checked + .answer-boolean {
            background: $brand-primary;
            color: #fff;
        }

        .answer-number {
            display: block;
            margin: 13px 0 0;
        }

        @media screen and (max-width: $screen-xs-max) {
            width: 100%;
        }
    }

    .economy {
        display: table;
        width: 780px;
        margin: 40px 0;
        color: $text-color;
        font-size: 22px;
        font-weight: 500;
        text-align: left;

        p {
            display: table-cell;
            vertical-align: top;
            margin: 0;

            &:last-child {
                padding-left: 150px;
                color: $brand-primary;
                font-size: 36px;
                font-weight: 300;
                text-align: right;

                @media screen and (max-width: $screen-xs-max) {
                    padding-left: 30px;
                }
            }
        }

        @media screen and (max-width: $screen-xs-max) {
            width: 100%;

            p {
                display: block;
            }
        }
    }

    .continue {
        display: none;
        margin: 40px 0 0;
        text-align: left;
    }
}