/* ======================================================
*  Fancybox
*  =====================================================*/

.fancybox-wrap,
.fancybox-skin,
.fancybox-outer,
.fancybox-inner,
.fancybox-image,
.fancybox-wrap iframe,
.fancybox-wrap object,
.fancybox-nav,
.fancybox-nav span,
.fancybox-tmp {
	padding: 0;
	margin: 0;
	border: 0;
	outline: none;
	vertical-align: top;
}

.fancybox-wrap {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 8020;
}

.fancybox-opened {
	z-index: 8030;
}

.fancybox-outer,
.fancybox-inner {
	position: relative;
}

.fancybox-inner {
	overflow: hidden;
}

.fancybox-type-iframe .fancybox-inner {
	-webkit-overflow-scrolling: touch;
}

.fancybox-error {
	color: #444;
	margin: 0;
	padding: 15px;
	white-space: nowrap;
}

.fancybox-image,
.fancybox-iframe {
	display: block;
	width: 100%;
	height: 100%;
}

.fancybox-image {
	max-width: 100%;
	max-height: 100%;
}

/* Close
---------------------------------------------- */
.fancybox-close {
	position: absolute;
	top: -18px;
	right: -18px;
	width: 36px;
	height: 36px;
	cursor: pointer;
	z-index: 8040;
}

/* Nav
---------------------------------------------- */
.fancybox-nav {
	position: absolute;
	top: 0;
	width: 40%;
	height: 100%;
	cursor: pointer;
	text-decoration: none;
	-webkit-tap-highlight-color: rgba(0,0,0,0);
	z-index: 8040;

	&:hover:after {
		opacity: 1;
	}

	&:after {
		@include transition(opacity 300ms ease-out);
		border-radius: 50%;
		color: #fff;
		content: '';
		display: block;
		font-size: 36px;
		line-height: 40px;
		font-family: 'FontAwesome';
		height: 40px;
		margin-top: -20px;
		position: absolute;
		text-align: center;
		text-shadow: 0 0 2px rgba(0,0,0,.4);
		top: 50%;
		width: 40px;
		opacity: 0;
	}
}

.fancybox-prev {
	left: 0;

	&:after {
		content: '\f104';
		left: 15px;
	}
}

.fancybox-next {
	right: 0;

	&:after {
		content: '\f105';
		right: 15px;
	}
}

/* Tmp
---------------------------------------------- */
.fancybox-tmp {
	position: absolute;
	top: -99999px;
	left: -99999px;
	visibility: hidden;
	max-width: 99999px;
	max-height: 99999px;
	overflow: visible !important;
}

/* Overlay
---------------------------------------------- */
.fancybox-lock {
    overflow: hidden !important;
    width: auto;
}

.fancybox-lock body {
    overflow: hidden !important;
}

.fancybox-lock-test {
    overflow-y: hidden !important;
}

.fancybox-overlay {
	background-color: rgba(0,0,0,.3);
	position: absolute;
	top: 0;
	left: 0;
	overflow: hidden;
	display: none;
	z-index: 8010;
}

.fancybox-overlay-fixed {
	position: fixed;
	bottom: 0;
	right: 0;
}

.fancybox-lock .fancybox-overlay {
	overflow: auto;
	overflow-y: scroll;
}

