/* ======================================================
*  Common
*  =====================================================*/

.main-content {
	z-index: 1;
	position: relative;
	padding-top: 0;
}

.section {
	padding: 40px 0 60px;
}

/* Blog Post Highlight */

.media.highlight > a {
	display: block;
	background: #cb003c;
}

.media.highlight h3 {
	margin: 0;
	padding: 25px 15px;
	background: #cb003c;
	color: #fff !important;
}

.media.highlight > div {
	padding: 0 15px 15px;
	background: #cb003c;
	color: #fff;
}

.media.highlight > div > a {
	color: #fff;
}