.modal {
    // fix a scroll bug in chrome
    &.in {
        transform: translateZ(0);
    }

    .modal-dialog {
        .modal-content {
            border-radius: 0;
            box-shadow: none;
        }
    }
}

.modal-iframe {
    iframe {
        border: 0;
        display: block;
        height: 100%;
        width: 100%;
    }
}

#modal-coverage-map {
    .modal-header {
        text-align: center;
    }

    .modal-body {
        padding: 0;

        #coverage-map {
            min-height: 360px;
        }
    }

    .modal-footer {
        text-align: center;

        .btn {}
    }
}