/* ======================================================
*  Profile
*  =====================================================*/

.profile-nav {
	@extend .list-unstyled;

	> li {
		margin-bottom: 15px;

		> a {
			color: $text-color;

			&:hover {
				color: $brand-primary;
			}
		}

		&.active {
			> a {
				color: $brand-primary;
			}
		}
	}
}

.tpl-user-information .users-billing-form {
	[name="payment_type"] {
		display: none;
	}
	
	[class^="payment-type"] {
		display: none;
	}
	
	#payment_type_credit-card:checked ~ .payment-type-credit-card {
		display: block;
	}

	#payment_type_sepa:checked ~ .payment-type-sepa {
		display: block;
	}

	.label-payment-type {
		display: table;
		width: 100%;
		table-layout: fixed;
		margin: 0 0 42px;
		
		label {
			display: table-cell;
			vertical-align: top;
			padding: 10px;
			border-top: 1px solid #b7b7b7;
			border-bottom: 1px solid #b7b7b7;
			border-left: 1px solid #b7b7b7;
			background: #fff;
			color: $text-color;
			font-size: 22px;
			font-weight: 500;
			text-align: center;
			cursor: pointer;
			
			&:first-child {
				border-bottom-left-radius: 5px;
				border-top-left-radius: 5px;
			}
			
			&:last-child {
				border-right: 1px solid #b7b7b7;
				border-bottom-right-radius: 5px;
				border-top-right-radius: 5px;
			}
			
			&:hover {
				background: #fe004b;
				color: #fff;
			}
			
			&:focus {
				border-color: #cbcbcb;
				box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 0 3px #cbcbcb;
				outline: none;
			}
		}
	}

	#payment_type_credit-card:checked ~ .label-payment-type [for="payment_type_credit-card"],
	#payment_type_sepa:checked ~ .label-payment-type [for="payment_type_sepa"] {
		background: $brand-primary;
		color: #fff;
	}
}
