.tpl-pages-pricing {
	.section-pricing-title {
		padding: 155px 0 0;
	}
	
	.order-storage-form {
		.section-storage {
			padding-top: 30px;
		}
		
		h2 {
			display: none;
		}
		
		.plans-detail {
			display: none;
		}
		
		#carousel-plans .plan {
			border: 0;
		}
		
		.calculator {
			display: block;
			border: 0;
			
			.text {
				display: block;
				padding-bottom: 13px;
				text-align: center;
			}
			
			.button {
				display: block;
				padding-top: 13px;
				padding-bottom: 10px;
				text-align: center;
			}
		}
	}
	
	.section-pricing-order .order-form {
		font-weight: 300;
		
		[name="zip_code"] {
			background: #f2f2f2;
		}
		
		a {
			font-weight: 500;
			color: $text-color;
		}
	}
	
	.section-pricing-delivery {
		img {
			margin: auto;
		}
		
		.title {
			font-size: 22px;
			font-weight: 500;
		}
		
		.subtitle {
			font-size: 22px;
		}
	}
	
	.section-billing {
		h2 {
			margin: 0 0 40px;
		}
		
		.well {
			margin-bottom: 0;
			padding: 0;
			background: none;
			border: 0;
			box-shadow: none;
			font-size: 22px;
		}
		
		.icon-wrapper {
			display: inline-block;
			vertical-align: middle;
			width: 40px;
			height: 40px;
			border: 2px solid $brand-green;
			margin-right: 15px;
			border-radius: 50%;
			line-height: 31px;
			text-align: center;
		}
		
		.icon-wrapper i {
			color: $brand-green;
			font-size: 14px;
		}
	}
	
	.section-pricing-calendar {
		padding: 30px 0 100px;
		
		.image-car img {
			margin: auto;
		}
		
		.title {
			margin: 40px 0 0;
			font-size: 36px;
		}
		
		.content {
			margin: 0 0 40px;
		}
		
		.calendar {
			.title {
				margin: 0 0 10px;
				font-size: 18px;
				font-weight: 500;
			}
			
			ul {
				list-style: none;
				margin: 0;
				padding: 0;
			}
			
			.regular-days {
				padding: 70px 20px 0 40px;
				
				.title {
					color: $brand-green;
				
					&:before {
						content: '';
						display: inline-block;
						vertical-align: middle;
						width: 24px;
						height: 24px;
						margin-right: 10px;
						background: $brand-green;
					}
				}
			}
			
			.busy-days {
				padding: 70px 0 0 20px;
				
				.title {
					color: $brand-gray2;
				
					&:before {
						content: '';
						display: inline-block;
						vertical-align: middle;
						width: 24px;
						height: 24px;
						margin-right: 10px;
						background: $brand-gray2;
					}
					
					&:after {
						content: '?';
						display: inline-block;
						vertical-align: middle;
						width: 16px;
						height: 16px;
						border: 1px solid $brand-green;
						border-radius: 50%;
						font-size: 10px;
						text-align: center;
						line-height: 13px;
					}
				}
			}
		}
	}
}
			
.section-pricing-schedule {
	.container {
		transform: translateY(-70px);
	}
	
	.image-car {
		img {
			width: 270px;
			margin: auto;
		}
	}
	
	.title {
		font-weight: 500;
	}
	
	.input-group-btn .btn.btn-primary {
		border: 2px solid #fff;
		padding: 10px 30px;
	}
	
	.alert-warning a {
		color: #000;
	}
}