/* Layout */

.tpl-order-calculator .footer .second-row {
	display: none;
}

/* Body */

.bg-gray-lighter-ultra {
	background: #f2f2f2;
}

.section-calculator {
	padding: 133px 0 37px;
	font-family: Museo;
	
	h1 {
		margin: 0 0 10px;
		font-size: 36px;
		font-weight: 300;
		text-align: center;
	}
	
	.subtitle {
		max-width: 450px;
		margin: 10px auto 35px;
		font-weight: 300;
	}
	
	.storing-duration {
		margin: 35px auto;
	}
	
	p {
		margin: 0;
	}
	
	@media screen and (max-width: $screen-xs-max) {
		padding: 90px 0 17px;
	}
}

.section-calculator-categories {
	margin: 0;
	padding: 37px 0 0;
	
	.category-wrapper {
		margin: 0 auto !important;
		box-shadow: none;
		background: #f2f2f2;
		
		.col-category {
			display: inline-block;
			vertical-align: bottom;
			float: none;
		}
		
		.row.hidden-xs {
			margin: 25px auto;
		}
	
		.category {
			display: block;
			width: 180px;
			margin: 0 auto 14px;
			cursor: pointer;
			
			svg * {
				stroke: $brand-primary;
			}
			
			.title {
				position: relative;
				margin: 10px 0 0;
				padding: 0 0 21px;
				color: $brand-primary;
				font-size: 22px;
				font-weight: 500;
				
				&:after {
					content: '';
					position: absolute;
					left: 0;
					right: 0;
					bottom: 0;
					display: block;
					width: 0;
					height: 0;
					margin: auto;
					border-top: 11px solid $brand-primary;
					border-right: 6px solid transparent;
					border-left: 6px solid transparent;
					
					@media screen and (max-width: $screen-xs-max) {
						display: block;
					}
				}
				
				.count {
					display: none;
					vertical-align: top;
					width: 20px;
					height: 20px;
					line-height: 20px;
					margin: 5px 0 0 4px;
					border-radius: 20px;
					background: $brand-primary;
					color: #fff;
					font-size: 14px;
					font-weight: 500;
					
					&.active {
						display: inline-block;
					}
				}
			}
			
			
			
			&.collapsed {
				svg * {
					stroke: #9d9d9d;
				}

				.title {
					color: $text-color;
				
					&:after {
						display: none;
						border-top: 0;
						border-bottom: 11px solid $text-color;
					}
				}
				
				&:hover {
					svg * {
						stroke: $brand-primary;
					}

					.title {
						color: $brand-primary;
					
						&:after {
							display: block;
							border-top: 11px solid $brand-primary;
							border-bottom: 0;
						}
					}
				}
			}
		}
		
		.items {
			background: #fff;
			
			.container {
				padding-top: 10px;
				padding-bottom: 10px;
				
				.item {
					margin: 12px 0;
				}
			}
			
			@media screen and (max-width: $screen-xs-max) {
				text-align: left;
			}
		}
	}
	
	p {
		margin: 0;
	}
}

.section-calculator-total {
	position: fixed;
	bottom: 0;
	left: 0;
	width: 100%;
	padding: 17px 0;
	box-shadow: 0 -2px 4px 0 rgba(0, 0, 0, 0.1);
	background: #fff;
	
	p {
		display: inline-block;
		vertical-align: middle;
		margin: 0;
	}
	
	.total-label {
		width: 141px;
		font-size: 22px;
		font-weight: 500;
		text-align: left;
		
		@media screen and (max-width: $screen-xs-max) {
			width: auto;
			margin: 0;
			font-size: 18px;
			font-weight: 300;
			text-align: center;
		}
	}
	
	.total-value {
		margin: 0 60px;
		font-size: 55px;
		font-weight: 300;
		color: $brand-primary;
		
		@media screen and (max-width: $screen-xs-max) {
			margin: 0;
			font-size: 18px;
		}
	}
	
	@media screen and (max-width: $screen-xs-max) {
		padding: 12px 0;
		
		.button-wrapper {
			display: block;
			margin-top: 20px;
		}
	}
}