/* Layout */

.tpl-order-storage {
    .main-header .navbar-right {
        display: none;
    }

    .navbar .order-need-help {
        display: block;
    }

    .footer .second-row {
        display: none;
    }
}

/* Body */

.order-storage-form .section-storage {
    padding: 70px 0 0;
}

.order-storage-form h1, .order-storage-form h2 {
    margin: 0 0 48px;
    color: $text-color;
    font-size: 36px;
    font-weight: 300;
}

.storage-supplies {
    margin: 35px 0 0;

    &.has-error {
        .item {
            label {
                color: $state-danger-text;

                svg {
                    [id="0_calculator"] {
                        stroke: $state-danger-border;
                    }
                }
            }
        }
    }

    .title {
        margin-bottom: 25px;
        font-size: 22px;
        font-weight: 500;
    }

    .item {
        display: inline-block;
        vertical-align: bottom;
        margin: 0 18px;

        @media screen and (max-width: $screen-xs-max) {
            display: block;
            margin: 20px 0;
            text-align: left;
        }
    }
}

.order-storage-form .carousel-plans {
    margin: 40px auto;
}

.order-storage-form #carousel-plans-mobile {
    width: 250px;
}

.order-storage-form #carousel-plans-mobile .carousel-control {
    top: 150px;
    width: 18px;
}

.order-storage-form #carousel-plans-mobile .carousel-control.left {
    left: -30px;
}

.order-storage-form #carousel-plans-mobile .carousel-control.right {
    right: -30px;
}

.order-storage-form .carousel-plans .carousel-indicators {
    position: static;
    display: inline-block;
    width: auto;
    margin: 0 0 56px;
    border-radius: 23px;
    background: #e6e6e6;
}

.order-storage-form .carousel-plans .carousel-indicators li {
    width: 125px;
    height: 43px;
    line-height: 43px;
    margin: 0;
    border: 0;
    border-radius: 23px;
    color: $text-color;
    text-indent: 0;
    font-size: 22px;
}

.order-storage-form .carousel-plans .carousel-indicators .active {
    background: #fff;
}

.order-storage-form .carousel-plans .carousel-control {
    top: 280px;
    bottom: auto;
    width: 27px;
    height: 46px;
    opacity: 100;
    filter: alpha(opacity=100);
    background: none no-repeat center center / 100% auto;
}

.order-storage-form .carousel-plans .carousel-control > * {
    display: none;
}

.order-storage-form .carousel-plans .carousel-control.left {
    left: -60px;
    background-image: url('../img/order/storage/picto-arrow-left.png');
}

.order-storage-form .carousel-plans .carousel-control.right {
    right: -60px;
    background-image: url('../img/order/storage/picto-arrow-right.png');
}

.order-storage-form .carousel-plans .plans {
    display: table;
    width: 100%;
    table-layout: fixed;
    border-spacing: 0 2px;
}


.plan {
    background: #fff;
    border: 1px solid #f2f2f2;
    display: block;
    margin: 0 auto;
    padding: 31px 43px;

    &.discount {
        .price {
            text-decoration: line-through;
        }
        .price-discount {
            display: block;
        }
    }

    .image {
        margin-bottom: 17px;

        img {
            margin: auto;
        }
    }

    .name {
        margin: 17px 0 2px;
        color: $text-color;
        font-size: 22px;
        font-weight: 500;
    }

    .price {
        margin: 2px 0 17px;
        color: $text-color;
        font-size: 22px;
        font-weight: 300;
    }

    .price-discount {
        display: none;
        margin: 0;
        color: $text-color;
        font-size: 22px;
        font-weight: 300;
    }

    .select-plan {
        margin: 17px 0 0;

        label {
            color: #fff;
        }
    }

    @media (max-width: $screen-xs-max) {
        max-width: 300px;
    }
    @media (min-width: $screen-sm-min) {}
    @media (min-width: $screen-md-min) {}
    @media (min-width: $screen-lg-min) {}
}

.order-storage-form .calculator {
    display: table;
    width: 100%;
    margin: 48px 0 42px;
    border: 1px solid #f2f2f2;
    background: #fff;
    text-align: left;
}

.order-storage-form .calculator > div {
    display: table-cell;
    vertical-align: middle;
    padding: 40px;
}

.order-storage-form .calculator .title {
    margin: 0;
    color: $text-color;
    font-weight: 500;
}

.order-storage-form .calculator .content {
    margin: 0;
    color: $text-color;
    font-size: 18px;
}

.order-storage-form .calculator .button {
    margin: 0;
    text-align: right;
}

.order-storage-form .plans-detail {
    display: table;
    width: 100%;
    margin: 42px 0 65px;
    text-align: left;
}

.order-storage-form .plans-detail .include,
.order-storage-form .plans-detail .contact {
    display: table-cell;
    vertical-align: top;
}

.order-storage-form .plans-detail .contact {
    width: 380px;
}

.order-storage-form .plans-detail .include .title {
    margin: 0 0 16px;
    color: $text-color;
    font-weight: 500;
}

.order-storage-form .plans-detail .include ul {
    margin: 0;
    padding: 0;
}

.order-storage-form .plans-detail .include li {
    color: $text-color;
    display: inline-block;
    font-size: 18px;
    vertical-align: top;
    width: 100%;
}

.order-storage-form .plans-detail .include li:before {
    content: '✓';
    display: inline-block;
    margin-right: 14px;
    color: #4bca84;
    font-weight: bold;
}

.order-storage-form .plans-detail .contact .title {
    margin: 0 0 16px;
    color: $text-color;
    font-weight: 500;
}

.order-storage-form .plans-detail .contact .content {
    margin: 16px 0 6px;
    color: $text-color;
    font-size: 18px;
}

.order-storage-form .plans-detail .contact .phone {
    margin: 6px 0 0;
    padding: 4px 0 4px 32px;
    background: url('../img/order/storage/group.png') no-repeat center left;
    color: $text-color;
    font-weight: 500;
}

.order-storage-form .plans-detail .contact .phone a {
    font-size: 18px;
}

.section-storage-confirmation {
    position: fixed;
    bottom: 0;
    left: 0;
    display: table;
    table-layout: fixed;
    width: 100%;
    padding: 0;
    box-shadow: 0 -2px 4px 0 rgba(0, 0, 0, 0.1);
    background: #fff;
    z-index: 2;

    .resume {
        position: relative;
        display: table-cell;
        vertical-align: top;
        width: 400px;
        padding: 56px 73px 44px;
        background: $brand-primary;
        color: #fff;
        text-align: left;

        .close {
            position: absolute;
            top: 13px;
            right: 10px;
            display: none;
            float: none;
            opacity: 1;
            width: 16px;
            height: 16px;
            font-weight: normal;
            text-shadow: none;

            &:before, &:after {
                position: absolute;
                top: -3px;
                left: 6px;
                content: '';
                height: 21px;
                width: 2px;
                background-color: #fff;
            }

            &:before {
                transform: rotate(45deg);
            }

            &:after {
                transform: rotate(-45deg);
            }

            @media screen and (max-width: $screen-xs-max) {
                display: block;
            }
        }

        .title {
            margin: 0;
            font-size: 22px;
            font-weight: 500;

            @media screen and (max-width: $screen-xs-max) {
                display: inline-block;
                font-size: 18px;
                font-weight: 300;
            }
        }

        .total {
            margin: 8px 0 0;
            font-size: 36px;
            font-weight: 300;

            @media screen and (max-width: $screen-xs-max) {
                display: inline-block;
                margin: 0;
                font-size: 18px;
            }
        }

        .items {
            margin: 36px 0 0;
            padding: 0;
            font-size: 14px;
            font-weight: 500;
            list-style: none;

            @media screen and (max-width: $screen-xs-max) {
                margin: 10px 0 0;
            }
        }

        @media screen and (max-width: $screen-xs-max) {
            display: block;
            width: 100%;
            padding: 10px 15px;
        }
    }

    .plan {
        display: table-cell;
        vertical-align: top;
        padding: 56px 30px 44px;
        text-align: left;

        .wrapper {
            display: table;
            vertical-align: top;
            margin: auto;

            .description {
                display: table-cell;
                vertical-align: middle;
                width: 600px;

                .title {
                    margin: 0;
                    color: $text-color;
                    font-size: 22px;
                    font-weight: 500;
                }

                .name {
                    margin: 8px 0 0;
                    color: $brand-primary;
                    font-size: 36px;
                    font-weight: 300;

                    @media screen and (max-width: $screen-xs-max) {
                        font-size: 26px;
                    }
                }

                .include {
                    margin: 41px 0 0;
                    font-size: 22px;
                    font-weight: 500;
                    color: $text-color;

                    @media screen and (max-width: $screen-xs-max) {
                        margin: 10px 0 0;
                    }
                }

                .assets {
                    font-size: .9em;
                    margin: 0;
                    padding: 0;
                    list-style: none;

                    li {
                        display: inline-block;
                        vertical-align: top;
                        //width: 50%;
                        margin: 11px 0 0 0;
                        color: $text-color;

                        &:before {
                            content: '✓';
                            display: inline-block;
                            vertical-align: top;
                            width: 20px;
                            height: 20px;
                            line-height: 19px;
                            margin-right: 10px;
                            border: 1px solid #4bca84;
                            border-radius: 20px;
                            color: #4bca84;
                            font-size: 14px;
                            text-align: center;
                        }

                        @media screen and (max-width: $screen-xs-max) {
                            display: block;
                            width: 100%;
                        }
                    }

                    @media screen and (max-width: $screen-xs-max) {
                        text-align: left;
                    }
                }

                @media screen and (max-width: $screen-xs-max) {
                    display: block;
                    width: 100%;
                }
            }

            .price-wrapper {
                display: table-cell;
                vertical-align: middle;
                padding: 50px;
                border-radius: 5px;
                border: solid 1px #e6e6e6;
                text-align: center;

                .price {
                    margin: 0;
                    color: $brand-primary;
                    font-size: 36px;
                }

                .price-discount {
                    display: none;
                    margin: 0;
                    color: $brand-primary;
                    font-size: 36px;
                }

                .submit-wrapper {
                    margin: 30px 0 0;
                }

                &.discount {
                    .price {
                        text-decoration: line-through;
                    }

                    .price-discount {
                        display: block;
                    }
                }

                @media screen and (max-width: $screen-xs-max) {
                    display: block;
                    width: 100%;
                    margin-top: 30px;
                    padding: 30px 0;
                }
            }

            @media screen and (max-width: $screen-xs-max) {
                display: block;
                text-align: center;
            }
        }

        @media screen and (max-width: $screen-xs-max) {
            display: block;
            width: 100%;
            padding: 10px 15px;
        }
    }

    @media screen and (max-width: $screen-xs-max) {
        top: 82px;
        display: block;
        overflow: auto;
    }
}

@media screen and (max-width: $screen-xs-max) {
    .order-storage-form .carousel-plans .carousel-indicators {
        display: none;
    }

    .order-storage-form .calculator,
    .order-storage-form .calculator > div,
    .order-storage-form .plans-detail,
    .order-storage-form .plans-detail .include,
    .order-storage-form .plans-detail .contact,
    .order-storage-form .plans-detail .include li {
        display: block;
    }

    .order-storage-form .calculator > div {
        padding: 20px;
    }

    .order-storage-form .calculator .button .btn {
        padding: 10px 15px;
        font-size: 16px;
    }

    .order-storage-form .plans-detail .include {
        margin-bottom: 20px;
    }

    .order-storage-form .plans-detail .include li {
        width: 100%;
    }

    .order-storage-form .plans-detail .contact {
        width: 100%;
    }
}