/* ======================================================
*  Helpers
*  =====================================================*/

.background,
.bg-full {
    @include bg-full();
}

.cursor-between{
    &:after{
        content: ">";
    }
}

.img-full{
    width: 100% !important;
}

.primary-color{
    color: $brand-primary!important;
}

.mt50 {
	margin-top: 50px;
}

.vertical-align {
    @include vertical-align();
}

.nomargin{
    margin: 0 !important;
}
.nopadding{
    padding: 0 !important;
}

.row.no-gutter {
    margin-left: 0;
    margin-right: 0;
}

.row.no-gutter [class*='col-']:not(:first-child),
.row.no-gutter [class*='col-']:not(:last-child) {
    padding-right: 0;
    padding-left: 0;
}


$ratio : 0, 10, 20, 25, 50, 75, 90, 100;
$margin : $ratio;

/**
* Vertical and horizontal helpers
*/
@each $item in $ratio {
    .vert-#{$item}{
        top: 1% * $item;
        position: absolute;
        text-align: center;
        width: 100%;
    }

    .hor-#{$item}{
        left : 1% * $item;
        position: absolute;
        text-align: center;
        width: 100%;
    }
}

/**
* Margin helpers
*/
@each $item in  $margin {
    @each $n in  'm', 'p' {
        .#{$n}t-#{$item}{
            margin-top: 1px * $item !important;
        }
        .#{$n}b-#{$item}{
            margin-bottom: 1px * $item !important;
        }
        .#{$n}l-#{$item}{
            margin-left: 1px * $item !important;
        }
        .#{$n}r-#{$item}{
            margin-right: 1px * $item !important;
        }
        .#{$n}-#{$item}{
            margin: 1px * $item !important;
        }

        .#{$n}t--#{$item}{
            margin-top: -1px * $item !important;
        }
        .#{$n}b--#{$item}{
            margin-bottom: -1px * $item !important;
        }
        .#{$n}l--#{$item}{
            margin-left: -1px * $item !important;
        }
        .#{$n}r--#{$item}{
            margin-right: -1px * $item !important;
        }
        .#{$n}--#{$item}{
            margin: -1px * $item !important;
        }
    }
}
