/* ======================================================
*  Header
*  =====================================================*/

.main-header{
    .navbar-default .navbar-collapse{
        max-height: 100vh !important;
    }
}

.navbar {
	border-bottom: 2px solid $gray-light;
    box-shadow: 0 0 2px rgba(0, 0, 0, .1);
    font-size: 18px;
    padding-left: 30px;
    padding-right: 30px;
	transition: all 0.5s;
	
	.navbar-brand {
		position: relative;
		width: 140px;
		padding: 23px 0;
		
		> img {
			position: absolute;
			top: 22.5px;
			left: 0;
			height: 35px;
			opacity: 0;
			transition: opacity 0.5s;
			
			&.sticky {
				opacity: 1;
			}
		}
	}
	
    .navbar-nav {
        padding-right: 15px;
		
		li a {
			font-weight: 500;
			transition: color 0.5s;
		}

        li.nav-color {
            > a {
				color: $brand-primary;
                padding-top: 23px;
                padding-bottom: 23px;
            }
            .fa {
				color: $brand-primary;
                font-size: 28px;
                position: relative;
                top: 3px;
            }
        }
		
		li.language-dropdown .dropdown-toggle {
			color: $brand-primary;
		}
		
        @media (max-width: $screen-md-min) {
            > li {
                > a {
                    padding-left: 10px;
                    padding-right: 10px;
                }
            }
        }
    }

    .navbar-toggle-mobile {
        position: relative;
        float: right;
        margin-right: 15px;
        padding: 0;
        margin-top: 14px;
        background-color: transparent;
        background-image: none;
        border: none;
        border-radius: 0;

        .nav > li > a {
            padding: 0;
            margin: 0;
        }

        .navbar-nav .open .dropdown-menu > li > a {
            padding: 5px 15px 5px 0 !important;
        }
    }

    @media (max-width: $screen-sm-min) {
        text-align: center;
    }
	
	

	@media screen and (max-width: $screen-xs-max) {
		padding-left: 15px;
		padding-right: 15px;
	}
	
	.navbar-toggle {
		margin-right: 0;
	}
	
	.order-need-help {
		display: none;
		float: right;
		padding: 27px;
		font-weight: 500;
	}
}

.navbar:not(.no-transition) {
	border-width: 0;
    box-shadow: 0 0 0 rgba(0, 0, 0, 0);
    border-bottom: 0;
	background-color: rgba(255, 255, 255, 0);
	
	.navbar-brand > img {
		opacity: 1;
		
		&.sticky {
			opacity: 0;
		}
	}
	
	&.sticky, &.open {
		background-color: rgba(255, 255, 255, 1);
		
		.navbar-nav {
			
			li a {
				color: $text-color !important;
				
				&:hover {
					color: $brand-primary !important;
				}
			}
			
			li.nav-color {
				> a {
					color: $brand-primary !important;
				}
				.fa {
					color: $brand-primary !important;
				}
			}
		}
		
		.navbar-brand > img {
			opacity: 0;
			
			&.sticky {
				opacity: 1;
			}
		}
	}
	
	.navbar-nav {
		li {
			a {
				color: $header-text-color !important;
				
				&:hover {
					color: $header-hover !important;
				}
			}
			
			.fa {
				color: $header-text-color !important;
			}
		}
		
		.dropdown-menu li a {
			color: $text-color !important;
			
			&:hover {
				color: $brand-primary !important;
			}
		}
	}
}

.navbar-btn {
    margin-top: 20px;
    padding: 5px 20px;

    @media (max-width: $screen-md-min) {
        padding-left: 10px;
        padding-right: 10px;
    }

    @media (max-width: $screen-sm-min) {
        margin-top: 0;
        margin-bottom: 0;
    }
}

/* Dropdown
---------------------------------------------- */
.dropdown-menu {
    left: 0;
    min-width: 0;
    margin-top: 1px;
    overflow: hidden;
    padding: 0;
    right: 0;
    text-align: center;

    > li {
        > a {
            color: $navbar-default-link-color;
            padding-top: 10px;
            padding-bottom: 10px;

            &:hover {
                color: $brand-primary;
            }
        }
    }
}

.name-dropdown{
    min-width: 150px;
}